.team {
    position: relative;
    padding-top: 50px;

    &::after {
        content: '';
        display: block;
        background: #CACADB;
        border-radius: 0px 0px 12px 12px;
        height: 10px;
        position: absolute;
        top: 0;
        bottom: 50%;
        left: 20px;
        right: 20px;

    }

    &::before {
        content: '';
        display: block;
        background: #E7E7F2;
        border-radius: 0px 0px 12px 12px;
        height: 10px;
        position: absolute;
        left: 40px;
        right: 40px;
        top: 10px;
        bottom: 0;


    }

    &__card {
        display: none;
        width: 100%;

        &--activated {
            display: block;

        }

        &-pic {
            height: 500px;
            width: 100%;

            background-image: url(../../img/team-face.jpg);
            background-size: cover;
            background-repeat: no-repeat;
            background-position: center;
            border-radius: 12px 12px 0px 0px;
            
        }

        &-text {
            padding-top: 27px;
            background-color: $grey-big-alt-color;
            border-radius: 0px 0px 12px 12px;
            padding-bottom: 30px;

        }

        &-box {
            margin: 0 30px;

            &-name {
                margin-top: 33px;
                font-weight: 700;
                font-size: 16px;
                line-height: 20px;
                color: #B3B3BA;

            }

            &-job {
                font-size: 14px;
                line-height: 22px;
                color: #B3B3BA;
                margin-top: 6px;

            }

        }

    }

}

@media  screen and (min-width: 1024px) {
    .team {

        &__card {
            
            &--activated {
                display: flex;

            }

            &-pic {
                width: 44%;
                height: auto;
                border-radius: 12px 0 0 12px;

            }
            &-text {
                width: 56%;
                border-radius: 0 12px 12px 0;

            }

        }

    }

}